<template>
  <div>
    <div class="item-box">
      <div class="item-title">角色一览</div>
      <div style="padding: 1rem">
        <input
          class="search-bar"
          placeholder="輸入角色名稱搜尋"
          type="text"
          v-model="searchInput"
        />
      </div>
      <div>
        <button
          class="switch-btn"
          :v-model="before_"
          v-bind:class="{ active: !before_ }"
          v-on:click="
            {
              {
                before_ = !before_;
                characterFilter(searchInput);
              }
            }
          "
        >
          前卫
        </button>
        <button
          class="switch-btn"
          :v-model="middle_"
          v-bind:class="{ active: !middle_ }"
          v-on:click="
            {
              {
                middle_ = !middle_;
                characterFilter(searchInput);
              }
            }
          "
        >
          中卫
        </button>
        <button
          class="switch-btn"
          :v-model="after_"
          v-bind:class="{ active: !after_ }"
          v-on:click="
            {
              {
                after_ = !after_;
                characterFilter(searchInput);
              }
            }
          "
        >
          后卫
        </button>
      </div>
      <div class="box">
        <table>
          <thead>
            <tr>
              <th v-for="key in columns" :key="key">
                {{ key }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="entry in newItems" :key="entry.id">
              <td>
                <div class="card" style="width: 55px">
                  <div style="height: 80%">
                    <img
                      :alt="entry.realName"
                      :title="entry.name"
                      v-lazy="entry.src"
                      class="card-img"
                    />
                  </div>
                  <div style="height: 20%">
                    <small>{{ entry.realName }}</small>
                  </div>
                </div>
              </td>
              <td>{{ entry.distance }}</td>
              <td>{{ entry.rank }}</td>
              <td>{{ entry.star }}</td>
              <td>{{ entry.unionEquipment }}</td>
              <td>{{ entry.inform }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
var chineseConv = require("chinese-conv");
const PinyinEngine = require("pinyin-engine/src/tw"); // 添加繁体
var pe = new PinyinEngine([]);

export default {
  name: "CharacterDisplay",
  components: {},
  data() {
    return {
      totalItems: [], //所有的角色
      items: [], // 显示的角色
      exlusionItems: [],
      columns: ["角色", "站位", "RANK推荐", "星数推荐", "专武推荐", "额外说明"],
      searchInput: "",
      before_: true, //前卫
      middle_: true, //中卫
      after_: true, //后卫
      before_limit: 300,
      middle_limit: 600,
    };
  },
  created: function () {
    this.$http.get("characters", (res) => {
      this.items = res.data;
      this.totalItems = this.items;
      pe = new PinyinEngine(this.totalItems, ["name"]);
    });
  },
  watch: {
    searchInput(newValue, oldValue) {
      if (newValue == oldValue) {
        return;
      }
      this.characterFilter(newValue);
    },
  },
  methods: {
    characterFilter(newValue) {
      this.items = [];
      if (newValue == "") {
        this.totalItems.forEach((i) => {
          // 过滤前中后卫的角色
          if (
            (!this.before_ && i.distance <= this.before_limit) ||
            (!this.middle_ &&
              i.distance > this.before_limit &&
              i.distance <= this.middle_limit) ||
            (!this.after_ && i.distance > this.middle_limit)
          ) {
            return;
          }
          this.items.push(i);
        });
      } else {
        //模糊搜索
        if (/^[a-zA-Z]+$/.test(newValue)) {
          let tempItems = pe.query(newValue);
          tempItems.forEach((i) => {
            // 过滤前中后卫的角色
            if (
              (!this.before_ && i.distance <= this.before_limit) ||
              (!this.middle_ &&
                i.distance > this.before_limit &&
                i.distance <= this.middle_limit) ||
              (!this.after_ && i.distance > this.middle_limit)
            ) {
              return;
            }
            this.items.push(i);
          });
        } else {
          let text = chineseConv.tify(newValue);
          this.totalItems.forEach((i) => {
            if (i.name.split(text).length > 1) {
              // 过滤前中后卫的角色
              if (
                (!this.before_ && i.distance <= this.before_limit) ||
                (!this.middle_ &&
                  i.distance > this.before_limit &&
                  i.distance <= this.middle_limit) ||
                (!this.after_ && i.distance > this.middle_limit)
              ) {
                return;
              }
              this.items.push(i);
            }
          });
        }
      }
    },
  },
  computed: {
    newItems() {
      this.items.forEach((element) => {
        element.src =
          "https://pcredivewiki.tw/static/images/unit/icon_unit_" +
          element.id +
          "31.png"; // 默认设置为3星的头像
      });
      return this.items;
    },
  },
};
</script>
<style scoped>
@import "../assets/css/CharacterDisplay.css";
</style>