<template>
  <div>
    <transition name="fade">
      <div class="modal" style="background: rgba(0, 0, 0, 0.5)" v-if="isShow">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="header">
              <span style="text-align: left">地图掉落</span>
              <toggle-button
                :value="enableEquipmentFilter"
                color="#82C7EB"
                :sync="true"
                :width="lableEquipmentFilter"
                :labels="{
                  unchecked: '不过滤低爆率装备',
                  checked: '过滤低爆率装备',
                }"
                @change="switchEquipmentFilter"
              />
              <button
                class="close"
                style="background-color: transparent; border: 0"
                v-on:click="close"
              >
                <span>x</span>
              </button>
            </div>
            <div class="item-box" style="overflow: auto; height: 400px">
              <table>
                <tr>
                  <th>地图</th>
                  <th style="text-align: left">掉落一览</th>
                </tr>
                <tr v-for="item in mapInfo" :key="item.title">
                  <th>{{ item.title }}</th>
                  <td>
                    <div style="display: flex; flex-wrap: nowrap">
                      <div
                        v-for="equipment in item.equipments"
                        :key="equipment.title"
                        style="
                          padding: 0.5rem;
                          display: flex;
                          flex-direction: column;
                        "
                      >
                        <img
                          :alt="equipment.title"
                          :title="equipment.title"
                          v-lazy="equipment.src"
                          width="55"
                          v-bind:class="{
                            map_equipment_no_target: !equipment.isSelect,
                          }"
                        />
                        <h6 style="margin: 0">{{ equipment.priority }}%</h6>
                      </div>
                    </div>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <div class="item-box">
      <button class="btn" v-on:click="submit">提交</button>
      <button
        class="btn"
        v-on:click="
          {
            {
              selectedItems.forEach((i) => {
                totalItems.forEach((t) => {
                  if (t.id == i.id) {
                    t.isSelect = false;
                    return true;
                  }
                });
              });
              selectedItems = [];
            }
          }
        "
      >
        清空
      </button>
    </div>
    <div class="item-box">
      <div class="item-title">已选择装备</div>
      <div
        v-if="
          selectedItems !== undefined &&
          selectedItems !== null &&
          selectedItems.length == 0
        "
      >
        请选择装备
      </div>
      <draggable v-else v-model="selectedItems" class="box">
        <div
          v-for="(item, index) in selectedItems"
          :key="item.id"
          class="item-div"
        >
          <img
            class="item-img equipment"
            :data-index="index"
            :data-select="item.isSelect"
            v-lazy="item.src"
            v-on:click="cancelClick(index)"
            :alt="item.title"
            :title="item.title"
          />
        </div>
      </draggable>
    </div>
    <div class="item-box">
      <div class="item-title">装备一览</div>
      <div style="padding: 1rem">
        <input
          class="search-bar"
          placeholder="输入装备名称搜索，如果选择了装备种类，输入框的数值将失效"
          type="text"
          v-model="searchInput"
        />
      </div>
      <div>
        <button
          class="btn"
          style="
            background: linear-gradient(180deg, #f55291 0, #ef4e4e);
            color: #fff;
          "
          v-on:click="
            {
              {
                cancelKindFilter();
                searchInput = ''; // 如果输入框有东西就清空
              }
            }
          "
        >
          清除筛选
        </button>
        <button
          class="btn"
          :style="kind.style"
          v-for="(kind, index) in kinds"
          :key="kind.name"
          v-on:click="
            {
              {
                if (filterIndex != -1) {
                  kinds[filterIndex].style = 'margin: 1% 4px;';
                }
                kinds[index].style =
                  'margin: 1% 4px;background:linear-gradient(180deg,#4db3f7 0,#4783f8);color: #fff;';
                filterIndex = index;
                if (searchInput == '') {
                  filterEquipmentByKind();
                } else {
                  searchInput = ''; // 如果输入框有东西就清空
                }
              }
            }
          "
        >
          {{ kind.name }}
        </button>
      </div>
      <div class="box">
        <div
          class="item-div"
          v-for="(item, index) in newItems"
          :key="item.title"
        >
          <img
            class="item-img equipment"
            :data-index="index"
            :data-select="item.isSelect"
            v-lazy="item.src"
            v-on:click="imgClick(index)"
            v-bind:class="{ active: item.isSelect }"
            :alt="item.title"
            :title="item.title"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
var chineseConv = require("chinese-conv");
const PinyinEngine = require("pinyin-engine/src/tw"); // 添加繁体
var pe = new PinyinEngine([]);
const cacheKey = "selectedItems";
export default {
  name: "EquipmentDispaly",
  components: {
    draggable,
  },
  watch: {
    searchInput(newValue, oldValue) {
      if (newValue == oldValue) {
        return;
      } else if (newValue == "") {
        this.filterEquipmentByKind();
      } else {
        //模糊搜索
        this.items = [];
        if (/^[a-zA-Z]+$/.test(newValue)) {
          this.items = pe.query(newValue);
        } else {
          let text = chineseConv.tify(newValue);
          this.totalItems.forEach((i) => {
            if (i.title.split(text).length > 1) {
              this.items.push(i);
            }
          });
        }
        if (this.filterIndex != -1) {
          var k = this.kinds[this.filterIndex].name;
          var temp = [];
          this.items.forEach((i) => {
            if (i.kind == k) {
              temp.push(i);
            }
          });
          this.items = temp;
        }
      }
    },
  },
  data() {
    return {
      totalItems: [], //所有的装备
      items: [], // 显示的装备
      selectedItems: [],
      isShow: false,
      display: "display:none",
      totalMapInfo: [], //总的地图信息
      mapInfo: [], //过滤后的地图信息
      enableEquipmentFilter: true,
      lableEquipmentFilter: 120,
      searchInput: "",
      kinds: [
        { name: "配件", style: "margin: 1% 4px;" },
        { name: "帽子", style: "margin: 1% 4px;" },
        { name: "鞋子", style: "margin: 1% 4px;" },
        { name: "盾", style: "margin: 1% 4px;" },
        { name: "衣服", style: "ma4px;" },
        { name: "恢復法杖", style: "margin: 1% 4px;" },
        { name: "攻击法杖", style: "margin: 1% 4px;" },
        { name: "斧", style: "margin: 1% 4px;" },
        { name: "枪", style: "margin: 1% 4px;" },
        { name: "弓", style: "margin: 1% 4px;" },
        { name: "拳套", style: "margin: 1% 4px;" },
        { name: "短剑", style: "margin: 1% 4px;" },
        { name: "刀", style: "margin: 1% 4px;" },
        { name: "剑", style: "margin: 1% 4px;" },
      ],
      filterIndex: -1,
    };
  },
  created: function () {
    this.$http.get("get", (res) => {
      this.items = res.data;
      this.totalItems = this.items;
      pe = new PinyinEngine(this.totalItems, ["title"]);
      //从本地拿缓存的数据，这里直接将序列化后的数组赋值的原因是由于img的url计算是在选择装备组件更新之后的，会导致url不正常
      var localCache = localStorage.getItem(cacheKey);
      if (localCache != undefined) {
        this.selectedItems = JSON.parse(localCache);
        this.selectedItems.forEach((i) => {
          this.items.forEach((t) => {
            if (t.id == i.id) {
              t.isSelect = true;
              return true;
            }
          });
        });
      }
    });
  },
  methods: {
    cancelKindFilter() {
      if (this.filterIndex != -1) {
        this.kinds[this.filterIndex].style = "margin: 1% 4px;";
        this.filterIndex = -1;
        this.items = this.totalItems;
      }
    },
    // 根据装备种类过滤装备列表
    filterEquipmentByKind() {
      if (this.filterIndex != -1) {
        var k = this.kinds[this.filterIndex].name;
        this.items = [];
        this.totalItems.forEach((i) => {
          if (i.kind == k) {
            this.items.push(i);
          }
        });
      } else {
        this.items = this.totalItems;
      }
    },
    // 过滤低爆率装备
    filterEquipment() {
      this.mapInfo = [];
      //遍历拿到的地图信息数组
      this.totalMapInfo.forEach((q) => {
        let count = 0;
        q.equipments.forEach((element) => {
          if (element.isSelect) {
            count += element.priority;
          }
          element.src = "https://pcredivewiki.tw/" + element.url;
        });
        if (!this.enableEquipmentFilter || count >= 36) {
          //没有开启过滤就直接加入
          //爆率大于36，说明至少有一件高爆率的装备
          this.mapInfo.push(q);
        }
      });
    },
    // 开关
    switchEquipmentFilter() {
      if (this.enableEquipmentFilter) {
        this.lableEquipmentFilter = 130;
      } else {
        this.lableEquipmentFilter = 120;
      }
      this.enableEquipmentFilter = !this.enableEquipmentFilter;
      this.filterEquipment();
    },
    close() {
      this.isShow = false;
      this.display = "display:none";
      this.mapInfo = [];
    },
    submit() {
      if (this.selectedItems.length === 0) {
        return;
      }
      localStorage.setItem(cacheKey, JSON.stringify(this.selectedItems));
      this.$http.post(
        "sendRI",
        {
          list: this.selectedItems.map((i) => i.title),
        },
        (res) => {
          this.totalMapInfo = res.data;
          if (this.totalMapInfo == null) {
            alert("不存在地图掉落信息");
            return;
          }
          this.filterEquipment();
          this.isShow = true;
          this.display = "display:block";
        },
        (ex) => {
          alert(ex);
        }
      );
    },
    cancelClick(index) {
      this.items.some((e) => {
        if (e.id == this.selectedItems[index].id) {
          e.isSelect = false;
          this.selectedItems.splice(index, 1);
          return true;
        }
      });
    },
    imgClick(index) {
      if (!this.items[index].isSelect) {
        this.selectedItems.push(this.items[index]);
        this.items[index].isSelect = true;
      } else {
        for (var i = this.selectedItems.length - 1; i >= 0; i--) {
          if (this.selectedItems[i].id === this.items[index].id) {
            this.selectedItems.splice(i, 1);
            this.items[index].isSelect = false;
            break;
          }
        }
      }
    },
  },
  computed: {
    newItems() {
      this.items.forEach((element) => {
        // element.src = "http://127.0.0.1/" + element.title + ".jpg";
        element.src = "https://pcredivewiki.tw/" + element.url;
      });
      return this.items;
    },
  },
};
</script>
<style scoped>
@import "../assets/css/EquipmentDisplay.css";
</style>